.home {
  color: #fff;
  padding: 50px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.5); /* Added for better readability */
}

.home h1 {
  font-size: 3em;
  margin-bottom: 20px;
}

.home .buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.home button {
  padding: 15px 30px;
  font-size: 1.2em;
  background-color: #1a0960;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.home button:hover {
  background-color: #133c13;
}






