    .take-quiz {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 20px auto;
    max-width: 600px;
  }
  
  .take-quiz h1 {
    font-size: 2em;
    margin-bottom: 20px;
    color: #333;
  }
  
  .take-quiz .question-block {
    margin-bottom: 20px;
  }
  
  .take-quiz .question-block p {
    font-size: 1.2em;
    margin-bottom: 10px;
    color: #555;
  }
  
  .take-quiz .question-block label {
    display: block;
    margin-bottom: 5px;
    font-size: 1em;
    color: #666;
  }
  
  .take-quiz input[type="radio"] {
    margin-right: 10px;
  }
  
  .take-quiz button {
    padding: 10px 20px;
    font-size: 1em;
    background-color: #1a0960;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-top: 10px;
  }
  
  .take-quiz button:hover {
    background-color: rgb(23, 22, 22);
  }
  
  .take-quiz p {
    font-size: 1.2em;
    color: #333;
  }
  
  .take-quiz .score {
    margin-top: 20px;
    font-size: 1.5em;
    color: #333;
  }
  
  .take-quiz .back-button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    background-color: #1a0960;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .take-quiz .back-button:hover {
    background-color: #133c13;
  }
  