.create-quiz {
    background-color: #7185e2;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    margin: 20px auto;
    max-width: 600px;
  }
  
  .create-quiz h1 {
    font-size: 2em;
    margin-bottom: 20px;
  }
  
  .create-quiz input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 1em;
  }
  
  .create-quiz button {
    padding: 10px 25px;
    font-size: 1em;
    background-color: #1a0960;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-top: 10px;
  }
  
  .create-quiz button:hover {
    background-color: #133c13;
  }
  